<template>
  <div class="about">
    <el-button class="bt" icon="el-icon-s-home" size="mini" @click="back">返回主页</el-button>
      <div class="input">
        <el-input @keyup.enter="search" placeholder="请输入内容" v-model="input3" class="input-with-select">
          <!-- <el-button slot="append" icon="el-icon-search"></el-button> -->
          <el-button @click="search" icon="el-icon-search" slot="append">搜索</el-button>
        </el-input>
      </div>
      <el-divider></el-divider>
      <div class="main">
        <Video v-for="(item,index) in videos" :key="index" @click.native="goto(item)" :up="item.author" :date="item.date" :title="item.title" :img="item.cover"></Video>
      </div>
      <Bottom></Bottom> 
  </div>
</template>
<script>
import Video from '../components/Video.vue'
import Bottom from '../components/Bottom.vue'
export default {
  components:{
    Video,
    Bottom
  },
  data(){
    return{
      input3:'',
      videos:[]
    }
  },
  created(){
    this.getData(this.$route.query.keyword)
  },
  methods:{
    async getData(key){
      const {data:res}=await this.$axios.get("/api/search",{params:{keywords:key}})
      this.videos=res.data
    },
     goto(item){
          this.$router.push({ path: '/video',query:{id:item}});
      },
      search(){
        this.getData(this.input3)
        // this.input3=''
      },
      back(){
        this.$router.push({path:'/'})
      }
  }
}
</script>
<style lang="less" scoped>
.about{
  width: 100%;
  .input{
    width: 600px;
    margin: 50px auto;
  }
  .bt{
    position: absolute;
    top: 10px;
    left: 50px;
  }
  .main{
    padding: 0  200px;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    Video{
      margin-right: 10px;
    }
  }
}
</style>
